import { addYears, getDecade, isValid, setYear } from 'date-fns';
import { isNil } from "../../../../utils/isNil";
/**
 *      
 */
export function getYearsOfDecade(range) {
    if (isNil(range) || !isValid(range)) {
        return [];
    }
    var decade = setYear(range, getDecade(range));
    var years = [];
    for (var i = 0; i < 10; i++) {
        years.push(addYears(decade, i));
    }
    return years;
}
