import { setDay } from 'date-fns';
/**
 *         -
 */
export function getWeekDays() {
    var date = new Date();
    var weekDays = [];
    for (var i = 1; i <= 7; i++) {
        weekDays.push(setDay(date, i)
            .toLocaleString('ru', { weekday: 'short' })
            .replace(/^./g, function (l) { return l.toUpperCase(); }));
    }
    return weekDays;
}
