import { isNil } from "../../../../utils/isNil";
import { isAfter, isSameDay, isValid } from 'date-fns';
import { preserveTime } from "../preserveTime";
/**
 *     .     ,   .
 *
 *     ,         .
 *          Fields/Range,   
 *  /      ,     
 */
export function orderInterval(interval) {
    if (isNil(interval)) {
        return null;
    }
    var date1 = interval.start, date2 = interval.end;
    if (isNil(date1) || isNil(date2)) {
        return { start: date1, end: date2 };
    }
    if (!(date1 && isValid(date1)) || !(date2 && isValid(date2))) {
        return null;
    }
    if (isAfter(date1, date2)) {
        if (isSameDay(date1, date2)) {
            return { start: date2, end: date1 };
        }
        return { start: preserveTime(date2, 'start', date1), end: preserveTime(date1, 'end', date2) };
    }
    return { start: date1, end: date2 };
}
