import { DATE_DISPLAY_FORMAT, DATE_RANGE_DISPLAY_FORMAT, DATE_TIME_DISPLAY_FORMAT, DATE_TIME_SS_DISPLAY_FORMAT, DATE_TIME_MS_DISPLAY_FORMAT, } from "../constants";
export function getDisplayFormat(withTime, precision, type) {
    if (type === 'range') {
        return DATE_RANGE_DISPLAY_FORMAT;
    }
    if (withTime) {
        if (precision === 'mm') {
            return DATE_TIME_DISPLAY_FORMAT;
        }
        else if (precision === 'ss') {
            return DATE_TIME_SS_DISPLAY_FORMAT;
        }
        else if (precision === 'ms') {
            return DATE_TIME_MS_DISPLAY_FORMAT;
        }
    }
    return DATE_DISPLAY_FORMAT;
}
