import { endOfDay, getHours, getMilliseconds, getMinutes, getSeconds, isValid, setHours, setMilliseconds, setMinutes, setSeconds, startOfDay, } from 'date-fns';
/**
 *   (..)      .
 *   ,       (00:00 / 23:59)
 */
export function preserveTime(targetDate, type, sourceDate) {
    if (!targetDate || !isValid(targetDate)) {
        return null;
    }
    if (!sourceDate) {
        if (type === 'start') {
            return startOfDay(targetDate);
        }
        return endOfDay(targetDate);
    }
    if (!isValid(sourceDate)) {
        return null;
    }
    var hours = getHours(sourceDate);
    var minutes = getMinutes(sourceDate);
    var seconds = getSeconds(sourceDate);
    var milliseconds = getMilliseconds(sourceDate);
    var withHours = setHours(targetDate, hours);
    var withMinutes = setMinutes(withHours, minutes);
    var withSeconds = setSeconds(withMinutes, seconds);
    return setMilliseconds(withSeconds, milliseconds);
}
