export function withTableStatics(component, statics) {
    var Component = component;
    var Container = statics.Container, THead = statics.THead, TBody = statics.TBody, Tr = statics.Tr, Th = statics.Th, Td = statics.Td, CollapsibleTr = statics.CollapsibleTr;
    Component.Container = Container;
    Component.THead = THead;
    Component.TBody = TBody;
    Component.Tr = Tr;
    Component.Th = Th;
    Component.Td = Td;
    Component.CollapsibleTr = CollapsibleTr;
    return Component;
}
