import isValid from 'date-fns/isValid';
import { isNil } from "../../../../utils/isNil";
/**
 *    .
 *   , :
 *  - null
 *  - undefined
 *  - Date
 *  - { start: Date, end: Date }
 */
export function isValidValue(value, type) {
    if (isNil(value)) {
        return true;
    }
    if (type === 'range') {
        var intervalValue = value;
        return isValid(intervalValue.start) && isValid(intervalValue.end);
    }
    return isValid(value);
}
